<template>
  <div>
    <b-table
      striped
      hover
      :items="rowsFile"
      :fields="headerFile"
      :per-page="perPage"
      :current-page="currentPage"
    ></b-table>
    <b-pagination
      v-model="currentPage"
      :total-rows="totalRows"
      :per-page="perPage"
      align="fill"
      size="sm"
      class="pt-5 pb-5 mt-5 mb-0"
      v-if="totalRows > 20"
    ></b-pagination>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "displayRowCsv",
  props: {
    items: {
      type: Array,
      required: true,
    },
    fields: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      currentPage: 1,
      perPage: 50,
    };
  },
  computed: {
    ...mapState(["headerFile", "rowsFile"]),
    totalRows() {
      if (this.rowsFile.length) {
        return this.rowsFile.length;
      } else {
        return 0;
      }
    },
  },
  methods: {
    //
  },
};
</script>
